export default {
  name: "Exhibitions",
  transition: "page",
  head () {
    return {
      title: 'Kat Lightfoot - Exhibitions',
    }
  },
  data () {
    return {
      slices: [],
      carousel: 0,
      animated: 'fade',
      drag: false,
      autoPlay: false,
      arrow: false,
      pauseHover: false,
      pauseInfo: false,
      pauseType: 'is-white',
      interval: 1000
    }
  },
  async asyncData({ $prismic, error }) {
    try {

      const document = (await $prismic.api.getSingle('exhibitions')).data

      return {
        document,
        slices: document.body
      };
    } catch (e) {
      error({ statusCode: 404, message: "Page not found" });
    }
  }
};
